.chip-wrapper{
  background-color: #000;
  height: 3000px;
}

#chip-section{
  position: sticky;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

video{
  height: 100vh;
  width:auto;
  position:relative;
  left: 50%;
  transform: translate(-50%,0);
}
.chip-wrapper h1{
  position: absolute;
  left: 0;
  top: 50%;
  z-index: 20;
  font-family: Helvetica;
  font-size: 64px;
  letter-spacing: -1px;
  text-align: center;
  width: 100%;
  transform: translate(0,-50%);
  margin: 0;
}

.chip-wrapper h1 span{
  display: inline-block;
  position: relative;
  color:transparent;
  background-image: linear-gradient(to right,white,white,transparent);
  background-size: 200% 100%;
  background-repeat: no-repeat;
  background-clip: text;
  background-position-x: 200%;
  animation: fillUp 2s ease-in-out 2s forwards;
}
.chip-wrapper h1 span::before{
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  -webkit-text-stroke: 2px;
  -webkit-text-stroke-color: white;
  -webkit-text-fill-color: transparent;
  animation: fadeIn 2s ease-in-out forwards;

}
.chip-wrapper h1 span:first-child::before{
  content: "iPhone 11 Pro called.";
}
.chip-wrapper h1 span:last-child::before{
  content: "It wants its chip back.";
}

#the-chip{
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 100px;
  height: 100px;
  box-shadow: 0px 0px 0px 100vw black, 0px 0px 0px 30px black inset;
  z-index: 10;
  min-width: 100px;
  min-height: 100px;
  background-color: #000;
  transform-origin: center;
}
#the-chip.transparent{
  background-color: transparent;
}

#A13{
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
}
#A13 .chip{
  fill:#fff;
}
#A13 .text-bg{
  fill:#fff;
}
#A13 .chip-mask{
  fill:#000;
}

@keyframes fillUp {
  from{
    background-position-x: 200%;
  }
  to{
    background-position-x: 0%;
  }
}

@keyframes fadeIn {
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
  
}