.sticky-container{
  font-family: Helvetica;
}
.homepod-section{

}
.homepod-container{
  width: 100%;
  height: 100vh;
  position: sticky;
  top: 0;
  z-index: -1;
}
.homepod-container.no-sticky{
  position: relative;
}

.homepod{
  width: 554px;
  height: 825px;
  position: absolute;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
}

.homepod div{
  width: inherit;
  height: inherit;
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  left: 0;
}

.device{
  background-image: url("/public/images/homepod/homepod.jpg");
}

.part-1{
  background-image: url("/public/images/homepod/homepod_1.jpg");
  opacity: var(--part-1);
}
.part-2{
  background-image: url("/public/images/homepod/homepod_2.jpg");
  opacity: var(--part-2);
}
.part-3{
  background-image: url("/public/images/homepod/homepod_3.jpg");
  opacity: var(--part-3);
}
.part-4{
  background-image: url("/public/images/homepod/homepod_4.jpg");
  opacity: var(--part-4);
}
.ending{
  background-image: url("/public/images/homepod/homepod.jpg");
  opacity: var(--ending);
}

.description-container{
  padding-bottom: 100vh;
}

.description{
  width: 490px;
  padding: 80px 50px;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 20px;
  text-align: center;
  color:#fff;
  margin:0 auto 100vh;
}

.description:last-child{
  margin-bottom: 0;
}

.description h3{
  font-size: 32px;
  margin: 0;
  padding: 0;
}

.description p{
  font-size: 18px;
  margin:12px 0 0;
}


.other-section{
  background: #0e0e0e;
  height: 200vh;
}
.img-box{
  width: 100%;
  height: 100vh;
  position: sticky;
  top: 0;
  overflow: hidden;
}
.img-box.no-sticky{
  position: relative;
}

.img-box::before,.img-box::after{
  content: "";
  width: 532px;
  height: 521px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  background-image: url("/public/images/homepod/homepod_5.jpg");
  background-repeat: no-repeat;
}
.img-box::before{
  filter: grayscale(100%);
}
.img-box::after{
  clip-path: inset(0 calc(100% - var(--slide)*1%) 0 0 );
}
.footer-section{
  height: 100vh;
  background-color: #eee;
}