.css-wrapper{
  font-family: Helvetica;
  min-height: 200vh;
}
.title{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 220px;
  position: relative;
  font-size: 60px;
  line-height: 1em;
  letter-spacing: -0.03em;
  margin: 10px auto;
}
.title svg{
  position: absolute;
  left: 0;
  bottom: -10px;
  z-index: -1;
}

#tick{
  stroke-dasharray: 210px;
  stroke-dashoffset: 210px;
  animation: tick 3s ease-in-out infinite;
}

@keyframes tick {
  0%,50%{
    stroke-dashoffset: 210px;
  }
  50%,100%{
    stroke-dashoffset: 0px;
  }
}

.like-wrapper{
  margin-top: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.like-wrapper svg{
  width: 100px;
  position: relative;
  z-index: 10;
}

.like-wrapper input[type="checkbox"]{
  display: none;
}

.like-wrapper label{
  cursor: pointer;
}

#heart{
  fill:#eee;
  stroke-width: 20px;
  stroke: red;
  stroke-dasharray: 1600px;
  stroke-dashoffset: 1600px;
  stroke-linecap: round;
}

@keyframes draw-heart {
  0%{
    stroke-dashoffset: 1600px;
  }
  80%{
    stroke-dashoffset: 0;
    fill:#eee;
  }
  100%{
    stroke-dashoffset: 0;
    fill:red;
  }
}
.like-wrapper input[type="checkbox"]:checked + svg{
  animation: pop 1s linear forwards;
}

.like-wrapper input[type="checkbox"]:checked + svg #heart{
  animation: draw-heart 1s linear forwards;
}

.like-wrapper span,.like-wrapper span::before{
  display: block;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: transparent;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  box-shadow: 0 -160px 0 red,0 160px 0 red,-160px 0 0 red,160px 0 0 red;
  transform: scale(0);
}
.like-wrapper span::before{
  content: "";
  transform: rotate(45deg) scale(0);
}

.like-wrapper input[type="checkbox"]:checked ~ span{
  animation: blink .5s ease-in-out forwards .85s;
}
.like-wrapper input[type="checkbox"]:checked ~ span::before{
  animation: blink-before .5s ease-in-out forwards .85s;

}

@keyframes blink {
  0%{
    transform: scale(0.5);
    opacity: 0.8;
  }
  50%{
    transform: scale(1);
    opacity: 0.8;
  }
  100%{
    transform: scale(1.1);
    opacity: 0;
  } 
}

@keyframes blink-before {
  0%{
    transform: rotate(45deg) scale(0.5);
    opacity: 0.8;
  }
  50%{
    transform: rotate(45deg) scale(1);
    opacity: 0.8;
  }
  100%{
    transform: rotate(45deg) scale(1.1);
    opacity: 0;
  }   
}


@keyframes pop {
  0%{
    transform: scale(1);
  }
  70%{
    transform: scale(1);
  }
  80%{
    transform: scale(1.1);
  }
  100%{
    transform: scale(1);
  }
}


.mouse-wrapper{
  margin-top: 300px;
  height: 160px;
  position: relative;
  overflow: hidden;
}
.mouse-wrapper div{
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;


  --offset:0px;
  --blur:2px;
}

.mouse-wrapper div img{
  display: block;
  width: 110%;
  height: 100%;
  object-fit: cover;

  transform: translateX(var(--offset));
  filter:blur(var(--blur))
}


.spotlight{
  margin-top: 300px;
  height: 100vh;
  background-color: #222;
  display: flex;
  align-items: center;
  justify-content: center;
}
.spotlight h2{
  color: #333;
  font-family: Helvetica;
  margin: 0;
  padding: 0;
  font-size: 100px;
  position: relative;
}

.spotlight h2::after{
  content:attr(data-spotlight);
  color: transparent;
  position: absolute;
  left: 0;
  top: 0;
  background-image: url("/public/images/text-bg.avif");
  background-size: 150%;
  background-position: center center;
  background-clip: text;
  -webkit-background-clip: text;
  clip-path:ellipse(100px 100px at 0% 50%);
  -webkit-clip-path:ellipse(100px 100px at 0% 50%);
  animation: spotlight 5s infinite;
}


@keyframes spotlight {
  0%{
    clip-path:ellipse(100px 100px at 0% 50%);
  -webkit-clip-path:ellipse(100px 100px at 0% 50%);
  }
  50%{
    clip-path:ellipse(100px 100px at 100% 50%);
  -webkit-clip-path:ellipse(100px 100px at 100% 50%);
  }
  100%{
    clip-path:ellipse(100px 100px at 0% 50%);
  -webkit-clip-path:ellipse(100px 100px at 0% 50%);
  }
}
