.page-wrapper {
  height: 1000vh;
}

.bg-wrapper {
  background-size: cover;
  background-repeat: no-repeat;
  position: sticky;
  top: 0;
  overflow: hidden;
  min-width: 1200px;
}

.sticky-bg {
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
  z-index: 2;
}

.image-box {
  opacity: 1;
  height: 100%;
  position: absolute;
  width: 100%;
  background-size: cover;
  background-position: 50%;
  transition: opacity 0.3s ease-in-out;
}

.head-box {
  height: 100%;
  position: absolute;
  width: 100%;
  background-size: cover;
  background-position: 50%;
  transition: opacity 0.3s ease-in-out;
}
.anime-wrapper{
  position: relative;
}
.text-wrapper {
  /* opacity: 0; */
  position: absolute;
  height: 100vh;
  width: 1200px;
  margin-left: -600px;
  left: 50%;
  display: flex;
  align-items: center;
}

.text-item {
  /* opacity: 0; */
  position: absolute;
  height: 100vh;
  width: 1200px;
  margin-left: -600px;
  left: 50%;
  display: flex;
  align-items: center;
  background: transparent;
}

.text-item-1 {
  z-index: 2;
}

.text-item-2,
.text-item-3 {
  /* transform: translateY(200px); */
  margin-top: 200px;
  z-index: 1;
  opacity: 0;
}

.item-cont {
  color: rgba(0, 0, 0, .5);
  font-size: 20px;
  letter-spacing: -.02em;
  width: 490px;
  line-height: 28px;
  text-align: left;
  margin-bottom: 24px;
}

.text-item.active {
  opacity: 1;
}


.frame-wrapper{
  position: absolute;
  top: 0;
  width: 100%;
  height: 100vh;
  transform: translate(0px, 0px);
  /* z-index: 9; */
}
.frame-canvas{
  display: block;
  margin: 0 auto;
  height: 100%;
  width: 100%;
  opacity: 0;
  object-fit: cover;
  transition: opacity 0.2s ease-out;
}
.prod-wrapper{
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  backface-visibility: hidden;
  perspective: 1000;
  transform-origin: center;
}
.prod-img-box{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.prod-img{
  width: 1026px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.inner-wrapper{
  opacity: 0;
  width: 100%;
  height: 100vh;
  position: absolute;
  z-index: 1;
}
.inner-bg-box{
  display: flex;
  justify-content: center;
  align-items: center;
}
.inner-sample-pic{
  width: 100%;
  height: 100vh;
  /* mask-repeat: no-repeat;
  mask-position: center; */
  position: relative;
}
.inner-sample-pic-wrapper{
  width: 100%;
  height: 100%;
}
.inner-pic-bg{
  height: 100%;
  background-size: cover;
  background-position: 50%;
}